@import 'scss/variables/index';

.pd-mobile-menu {
  background-color: $pd-color-white;
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 3;

  .header {
    font-size: 25px;
    font-weight: 600;
    margin: $spacing-lg 0;

    .side-btn {
      cursor: pointer;
      position: absolute;
      right: $spacing-md;
      z-index: 1;

      &--right {
        font-size: $pd-font-size-sm;
        font-weight: 500;
        margin-top: 6px;
        right: 20px;
      }
    }
    .title {
      color: $pd-color-darker-gray;
      text-align: center;
    }
  }

  .mobile-menu {
    padding: 0px 20px;

    a {
      display: block;

      &:not(:last-of-type) {
        border-bottom: solid 1px $pd-color-gray;
      }
    }

    .menu {
      font-size: 16px;
      list-style: none;
      padding: $spacing-md $spacing-sm;

      &:hover {
        background: rgba($primary-color, 0.05);
      }
    }
  }
}
