@import 'scss/variables/index.scss';

$font: cursive;
$font-title-size: 60px;

.pd-home {
  &-introduction {
    background-color: $secondary-color;

    &-wrapper {
      display: flex;
      flex-direction: column;
      height: 100%;
      justify-content: center;

      &-title {
        font-size: $font-title-size;
      }

      &-subtitle {
        font-size: 20px;
        letter-spacing: 3px;
      }

      &-text {
        font-size: 16px;
      }

      div {
        margin: $spacing-xs 0;
      }

      .ant-btn {
        background-color: transparent;
      }
    }

    img {
      width: 100%;
    }
  }

  &-product {
    font-family: $font;
    margin: $spacing-2xl 0;

    &-wrapper {
      cursor: pointer;
      display: block;
      padding: $spacing-lg 0;

      &:hover {
        color: $primary-color;
        transition: color 0.2s ease;
      }
    }

    &-title {
      color: $primary-color;
      font-size: $font-title-size;
      text-align: center;
    }

    &-subtitle {
      font-size: 28px;
    }

    .ant-col {
      margin: $spacing-lg 0;
      text-align: center;
    }

    img {
      height: 100px;
    }
  }

  &-about {
    margin: $spacing-2xl 0;

    &-title {
      color: $primary-color;
      font-family: $font;
      font-size: $font-title-size;
      font-style: italic;
      text-align: center;
    }

    &-subtitle {
      font-size: 28px;
      letter-spacing: 4px;
    }
  }

  &-contact {
    margin: $spacing-2xl 0;

    &-title {
      color: $primary-color;
      font-size: $font-title-size;
      text-align: center;
    }

    &-wrapper,
    iframe {
      margin: $spacing-xl 0;

      .anticon {
        margin: $spacing-md $spacing-lg 0 $spacing-md;
      }
    }
  }
}
