@import 'scss/variables/index.scss';

.pd-header {
  background: white;
  color: $pd-color-light-gray;
  padding: $spacing-md $spacing-lg;

  a {
    &:hover {
      color: $pd-color-white;
    }
  }

  &-logo {
    color: $primary-color;
    font-size: 30px;

    a:hover {
      color: $primary-color;
    }
  }

  &-right {
    text-align: right;

    a {
      color: $pd-color-dark-gray;
      margin: 0 $spacing-md;

      &:hover {
        color: $primary-color;
      }
    }
  }
}
