@import 'scss/variables/index.scss';

.pd-footer {
  font-family: sans-serif;
  background: white;
  padding: $spacing-lg;

  a {
    &:hover {
      color: $primary-color;
    }
  }

  .ant-row {
    margin-bottom: $spacing-lg;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .title {
    font-weight: bold;
  }

  .list {
    display: block;
    padding: 0px;

    li {
      list-style: none;
    }
  }

  &-center {
    text-align: center;
  }
}
